<template>
  <div class="home">
    <p class="text-4xl text-MyColor-H3 font-bold pt-4">
      論小型自動化自娛娛人歡樂盆栽實行遠種植之開開心心零距離培養計畫
    </p>
    <p class="text-3xl text-MyColor-H5">環境</p>
    <br />
    <p>土壤濕度 : {{ soli }}%</p>
    <p>環境溫度 : {{ dht.temperature }}</p>
    <p>環境濕度 : {{ dht.humidity }}%</p>
    <p>亮度 : {{ light }}</p>
    <br />
    <button
      class="m-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      @click="clean()"
    >
      關閉水磊
    </button>
    <button
      class="m-4 bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded"
      @click="Open()"
    >
      開啟水磊
    </button>
    <br />
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "Environment",
  components: {},
  data() {
    return {
      soli: 0,
      light: 0,
      dht: { temperature: 0, humidity: 0 },
      relay: null
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let _this = this;
      window.boardReady({ device: "aVLvB", multi: true }, function(board) {
        board.samplingInterval = 250;
        _this.soli = new window.getSoil(board, 0, 0);
        _this.soli.on(function(val) {
          _this.soli = val;
          _this._watcher.run();
        });
        _this.light = new window.getPhotocell(board, 5);
        _this.light.on(function(val) {
          _this.light = val;
          _this._watcher.run();
        });
        _this.dht = new window.getDht(board, 11);
        _this.dht.read(function(evt) {
          _this.dht = evt;
        }, 1000);
        _this.relay = new window.getRelay(board, 10);
        _this.relay.on();
      });
    },
    Open() {
      this.relay.off();
    },
    clean() {
      this.relay.on();
    }
  }
};
</script>
